                this.copyFrom.copyHouse = true;
            }else {
                this.copyFrom.copyHouse = false;
            }
        },
        clickitem1(e){
            e === this.ridor2 ? this.ridor2 = '' : this.ridor2 = e
            //console.log(this.copyFrom.saveOldDate);
            if(this.ridor2){
                this.copyFrom.saveOldDate = true;
            }else {
                this.copyFrom.saveOldDate = false;
            }
        },
        //
        open(title) {
            this.$message(title);
        },
        queding1(){
            this.dialogFormVisible1 = false;
        },
        //
        queryTungList(){
            this.communityBuilding(1,10);
            // this.getTungList(1,10);
            this.open('');
        },
        // 
        doubleClick(row){
            console.log(row);
            var that = this;
            that.show = 3;
            let params = {
                buildingGuid : row.guid,
            }
            http.doubleClick(params).then(res => {
                console.log(res);
                that.tungName = res.name;
                that.dtoMessage = res;
                this.$store.commit('add_tungObj',res);
            });
        },
        //
        addBuilding(){
            var that = this;
            let dto = that.dto;
            http.addBuilding(dto).then(res => {
                console.log(res);
                this.$store.commit('add_tungObj',res);
                that.open('');
              
            });
        },
        // 
        addBuilding2(){
            var that = this;
            let dto = that.tungListObj;
            if(JSON.stringify(dto) == "{}") {
                that.open('');
            }else{
                 http.addBuilding(dto).then(res => {
                     console.log(res);
                     that.open('');
                     this.$store.commit('add_tungObj',res);
                     that.dialogFormVisible = false;
                    // if(res.status == 'success') {
                    //     that.open('');
                    // }else{
                    //     that.open('');
                    // }
                    // that.dialogFormVisible = false;
                    // that.getTungList(1,10);
                });
            }
           
        },
        //
        getBuildingBasics(){
            var that = this;
            let params = {
                buildBasic:1
            };
            http.getBuildingBasics(params).then(res => {
                 that.options.buildingBasics = tool.changeData(res);
            });
        },
        //
        getIsElevator(){
            var that = this;
            let params = {
                elevator:true
            };
            http.getIsElevator(params).then(res => {
                 that.options.isElevator = tool.changeData(res);
            });
        },
        //
        getTungAscription(){
            var that = this;
            let params = {};
            http.getTungAscription(params).then(res => {
                 that.options.tungAscription = tool.changeData(res);
            });
        },
        //
        getTungUser(){
             var that = this;
            let params = {
                buildPurpose:1
            };
            http.getTungUser(params).then(res => {
                 that.options.tungUser = tool.changeData(res);
            });
        },
        //
        getStructure(){
             var that = this;
            let params = {
                construction:1
            };
            http.getStructure(params).then(res => {
                 that.options.structure = tool.changeData(res);
            });
        },
        //
        getBottomUse(){
            var that = this;
            let params = {
                groundFloorUse:1
            };
            http.getBottomUse(params).then(res => {
                 that.options.bottomUse = tool.changeData(res);
            });
        },
       //
        deteleTung(){
            var that = this;
            let arr = [];
            if(that.multipleSelection.length != 0) {
                that.multipleSelection.forEach(function (ele,index) {
                    arr.push(ele.code);
                })
                let buildingGuids = arr;
                that.dialogFormVisible1 = false;
                // console.log(buildingGuids);
                http.deteleTung(buildingGuids).then(res => {
                    that.open(res);
                    // that.getTungList(1,10);
                    this.communityBuilding(1,10);
                });
            }else{
                that.open('');
            }
             that.dialogFormVisible1 = false;
           
        },
        //
        getTungList(pageIndex,pageSize){
            var that = this;
            let params = {
                code:that.form.code,
                name : that.form.name,
                unitCount : that.form.unitCount,
                landNo :that.form.landNo,
                communityGuid:that.form.communityGuid,
                buildPurpose:that.form.buildPurpose,
                floorCount:that.form.floorCount,
                pageIndex : pageIndex,
                pageSize : pageSize,
            }
             http.getTungList(params).then(res => {
                 console.log(res);
                that.tungListData = res.content;
            });
                
        },
        //
        communityBuilding(pageIndex,pageSize){
            var that = this;
            let data = {
                code:that.form.code,
                name : that.form.name,
                unitCount : that.form.unitCount,
                landNo :that.form.landNo,
                communityGuid:this.quartersObj.guid,
                buildPurpose:that.form.buildPurpose,
                floorCount:that.form.floorCount,
                pageIndex : pageIndex,
                pageSize : pageSize,
            }
             http.communityBuilding(data).then(res => {
                 console.log('',res);
                    
                 that.tungListData = res.content;
                 that.pageObj.total = res.totalPages;
                 that.pageObj.pageSize = res.size;
                 that.pageObj.currentPage4 = res.number + 1;
            });
                
        },
        // 
        copyTung(){
            var that = this;
            that.copyFormVisible = false;
            let data = that.copyFrom;
            http.copyTung(data).then(res => {
                 console.log('',res);
            });
          
        },
        copyqueding(){
            
        },
        //
        query(){
            var that = this;
            if(that.bullidID != ''){
                var params = {
                    bullidID: that.bullidID
                }
                http.query(params).then(res => {
                    console.log('',res);
                    that.tungListObj = res;
                    that.tungList = [];
                    that.tungList.push(res);
                });
            }else {
                that.open('ID!');
            }
        },
        //
        getTung(optionsName,data){
            var that = this;
            that.isTungListShow = true;
            that[optionsName] = data;
            console.log(optionsName,data);
        },
        //
        getOptions(optionsName,optionsData) {
            var that = this;
            that.options[optionsName] = optionsData;
        },
        //
         open(title) {
            this.$message(title);
        },
        //
        queding(){
            this.dialogFormVisible = false;
            //
            this.$store.commit('is_pop');
            //
           
        },
        add(){
            this.show = 2;
        },
        fanhui(){
            this.show = true;
        }
    },
    created(){
        //this.deteleTung1();
        this.communityBuilding(this.pageObj.currentPage4,this.pageObj.pageSize);
        //  //
        // this.getTungList(1,10);
        //
        this.getTungUser();
        //
        this.getStructure();
        //
        this.getBottomUse();
         //
        this.getTungAscription();
         //
        this.getIsElevator();
        //
        this.getBuildingBasics();
        this.dto.regionDistrictGuid = this.quartersObj.code;
        this.dto.landNo = this.quartersObj.landNo;
    },
    computed:{
     ...mapState({
        quartersObj: state => state.quartersObj
     })
    }
  };
</script>
<style lang="scss" scoped>
#tungMessage{
    
    .content_d{
        width: calc(100% - 239px);
        float: left;
        margin: 20px 0px 0 15px;
         border: 1px solid #ccc;
        box-shadow: 0 0 3px #ccc;
        .page_box{
            display: inline-block;
            float: right;
            margin-top: 14px;
        }
        h2 {
            width: 100%;
            height: 35px;
            background-color: #f5f7fa;
            p {
                display: inline-block;
                width: auto;
                padding: 0;
                border-top: 2px solid #0471dd;
                background: #fff;
                height: 35px;
                .tab_name {
                    display: inline-block;
                    box-sizing: border-box;
                    float: left;
                    height: 35px;
                    line-height: 35px;
                    padding: 0 20px;
                    color: #0471dd;
                    font-weight: 400;
                    font-size: 13px;
                    width: auto;
                }
                .close_img {
                    width: 20px;
                    height: 35px;
                    line-height: 35px;
                    display: inline-block;
                    float: left;
                    box-sizing: border-box;
                    background: url('../../static/images/close3.png') no-repeat left center;
                }
            }
            p.noactive{
                background:rgba(247, 247, 247, 1);
                border-top: none;
                span{
                    color:rgba(153, 153, 153, 1);
                    font-weight: 400;
                }
            }
        }
    }
 .changci_content_left {
    width: 100%;
    float: left;
    background: #fff;
    box-sizing: border-box;
    padding: 0px 18px;
    min-height: 491px;
    p{
        margin:10px 0px;
        &>.button_right{
            float: right;
        }
        &>.add{
            background: yellow;
            color: #000;
        }
        &>.delete{
            background: red;
        }
    }
    // 
    .search_box {
        width: 100%;
        height: auto;
        box-sizing: border-box;
        padding: 0 0px;
        border-bottom: 1px solid #eeeeee;
        background: #fff;
        margin-top: 15px;
        .search {
            width: 100%;
            height: auto;
            li {
                float: left;
                width: 25%;
                height: 30px;
                line-height: 30px;
                margin-bottom: 15px;
                span {
                    display: inline-block;
                    float: left;
                    height: 30px;
                    line-height: 30px;
                    text-align: right;
                    margin-right: 5%;
                    width:48px;

                }
                div{
                    float: left;
                    /deep/ .el-input .el-input__inner{
                        height: 30px;
                        line-height: 30px;
                    }
                    .homePage /deep/ .el-date-picker {
                        padding: 0;
                        background: red;
                    }
                    .el-select /deep/ .el-input{
                            height: 35px;
                    }
                }
               
            }
        }
    }
    .margin_box{
        margin-top: 50px;
    }
    //
    .quarterMessage_box{

        /deep/ .el-table {
            th{
                padding: 6px 0!important;
            }
            .cell{
                text-align: center;
            }
        }
        /deep/ .demo-ruleForm{
             .el-form-item.is-required:not(.is-no-asterisk)>.el-form-item__label:after{
                content: '*';
                color: #f56c6c;
                margin-left: 4px;
            }
            .el-form-item.is-required:not(.is-no-asterisk)>.el-form-item__label:before{
                content: '';
            }
            .el-form-item{
                width: 50%;
                float: left;
                margin-bottom: 0px;
                .el-form-item__content{
                        // width: 50% !important;
                        line-height: 30px;
                        text-align: center;
                        margin-left: 50%!important;
                    .shengcheng{
                        // width: 77%;
                        
                    }
                    /deep/ .el-button{
                        position: absolute;
                        top: -36px;
                        left: 5px;
                    }
                    .danwei{
                        // width: 83%!important;
                        
                    }
                    .ping{
                        position: absolute;
                        top: 5px;
                        right: 5px;
                    }
                    .address{
                        width:100%;
                    }
                    .el-form-item{
                        width:100%;
                    }
                    .el-select{
                        width: 100%;
                    }
                    .el-input__icon{
                            line-height: 30px!important;
                        }
                    .el-form-item__content{
                        margin-left:0px !important;
                    }
                    /deep/ .el-input{
                                border-right: 1px solid rgb(222, 230, 249);
                            border-bottom: 1px solid rgb(222, 230, 249);
                        }
                    .el-input__inner{
                        height: 38px;
                        line-height: 38px;
                        border:none;
                    }
                    .el-textarea{
                            border-right: 1px solid rgb(222, 230, 249);
                        border-bottom: 1px solid rgb(222, 230, 249);
                        border-top: 1px solid rgb(222, 230, 249);
                        textarea{
                            border:none;
                        }
                        
                    }
                    .el-form-item__error{
                        display: none;
                    }
                }
                .el-form-item__label{
                    width:50%!important; 
                    line-height: 38px;
                    background: rgb(240, 248, 255);
                    text-align: center;
                    border-right: 1px solid rgb(222, 230, 249);
                    border-bottom: 1px solid rgb(222, 230, 249);
                }
            }
            .readyOnly{
                .el-input__inner{
                    background: rgb(245, 247, 250);
                }
            }
            .width{
                width: 100%;
                .el-form-item__label{
                    width: 25% !important;
                }
                .el-form-item__content{
                    width:75% !important;
                    margin-left: 25% !important;
                }
            }
            .xiangqing{
                .el-form-item__label{
                    line-height: 53px !important;
                }
            }
            .button{
                .el-form-item__content{
                    margin-left:0px !important;
                    float:none !important;
                }
                
            }
        }

        
    }
    
    }
    #copyFormVisible{
        td>div{
            height: 36px;
            line-height: 36px;
            box-sizing: border-box;
            padding:2px;
            .left{
                float: left;
                height: 100%;
                line-height: 32px;
            }
            .right{
                float:left;
                height: 100%;
                line-height: 32px;
                width: 90%;
                input{
                    box-sizing: border-box;
                    border:1px solid #ccc;
                    height: 28px;
                    width:90%;
                }
            }
        }
    }
    /deep/ .el-dialog__wrapper {
            .el-dialog__header{
                background: #057ff8;
                padding-top:10px;
                .el-dialog__title{
                    color:#fff;
                }
            }
            .el-dialog__headerbtn{
                top:13px;
                .el-dialog__close{
                    color:#fff;
                }
            }
            .el-dialog__body{
                padding:10px 20px;
                .el-table {
                    th{
                        padding:6px 0;
                    }
                    .cell{
                        text-align: center;
                    }
                }
                
                .el-form{
                    
                    margin-bottom: 10px;
                    .border_box{
                        border-top:1px solid #ccc;
                        border-bottom:1px solid #ccc;
                        .copy_width{
                            width: 94%;
                        }
                    }
                    p{
                        text-align: center;
                        font-weight: 400;
                        font-style: normal;
                        font-size: 16px;
                        color: #333333;
                        margin-bottom: 10px;
                    }
                    .search_input {
                        display: inline-block;
                        width: 300px;
                        margin-left:32%;
                        input{
                            border: 1px solid #ccc;
                            padding:7px 15px;
                            width: 240px;
                        }
                    }
                }
                .copy_box{
                    border: 1px solid #ccc;
                    .el-form-item{
                        margin-bottom: 0px;
                        .el-form-item__content{
                            line-height: 30px;
                        }
                        .el-form-item__label{
                            line-height: 30px;
                            text-align: center;
                        }
                        .el-input__inner{
                            height: 30px;
                            line-height: 30px;
                        }
                    }
                }
            
            }
            .dialog-footer{
                text-align: center;
            }
        }


}
</style>



