<template>
    <!--  -->
    <div id="quartsMessage">
        <!-- tab -->
            <h2  style="display:none">
                <p :class="classB"> <router-link :to="{path: '/housinginformation', query:{}}"><span></span></router-link><span class="close_tab"></span></p>
                <p :class="classA" style=""><span></span><router-link :to="{path: '/housinginformation', query:{}}"><span class="close_tab"></span></router-link></p>
            </h2>
        <!-- tab -->
        <div class="content_d">
            <h2>
                <p><span class="tab_name"></span><span class="close_img"></span></p>
            </h2>
            <div class="changci_content_left">
                <div class="quarterMessage_box">
                    <el-form :model="dto" :rules="rules" ref="dto" label-width="100px" class="demo-ruleForm">
                        <el-form-item class="width button" style="float:none">
                            <el-button type="primary" @click="submitForm('dto')" size="mini" class="save"></el-button>
                        </el-form-item>
                        <div style="border-top:1px solid rgb(222, 230, 249);border-left:1px solid rgb(222, 230, 249);" class="clearfix">
                        <el-form-item label="" prop="name" class="readyOnly">
                            <el-input v-model="dto.name" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="name">
                            <el-select v-model="dto.region" placeholder="">
                                <el-option label="" value="shanghai"></el-option>
                                <el-option label="" value="beijing"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="code">
                            <el-input v-model="dto.code" class="shengcheng"></el-input>
                            <el-button type="primary" @click="getDistrictCode" size="mini"></el-button>
                        </el-form-item>
                        <el-form-item label="" prop="name">
                            <el-input v-model="dto.name"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="landNo" class="width">
                            <el-input v-model="dto.landNo"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="belongArea">
                            <el-select v-model="dto.belongArea" placeholder="">
                                <el-option v-for="item in options.communityOwner" :key="item.value" :label="item.label" :value="item.value"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="area">
                            <el-select v-model="dto.area" placeholder="">
                                 <el-option v-for="item in options.area_prepar" :key="item.value" :label="item.label" :value="item.value"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="street">
                            <el-input v-model="dto.street"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="roadName">
                            <el-input v-model="dto.roadName"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="address" class="width">
                            <el-input v-model="dto.address" class="address"></el-input>
                            <el-button type="primary" @click="getAddress" size="mini"></el-button>
                        </el-form-item>
                        <el-form-item label="" prop="landArea">
                            <el-input v-model="dto.landArea" class="danwei"></el-input>
                            <span class="ping"></span>
                        </el-form-item>
                        <el-form-item label="" prop="totalCoveredArea">
                            <el-input v-model="dto.totalCoveredArea" class="danwei"></el-input>
                            <span class="ping"></span>
                        </el-form-item>
                        <el-form-item label="" prop="volumeRatio">
                            <el-input v-model="dto.volumeRatio"></el-input>
                        </el-form-item>
                        <el-form-item label="" required>
                            <el-form-item prop="startConstructionTime">
                                <!-- <el-date-picker type="date" placeholder="" v-model="ruleForm.date1" style="width: 100%;"></el-date-picker> -->
                                <el-date-picker  value-format="yyyy-MM-dd" type="date" placeholder="" v-model="dto.startConstructionTime" style="width: 100%;"></el-date-picker>
                            </el-form-item>
                        </el-form-item>
                        <el-form-item label="" required>
                            <el-form-item prop="finishConstructionTime">
                                <el-date-picker value-format="yyyy-MM-dd" type="date" placeholder="" v-model="dto.finishConstructionTime" style="width: 100%;"></el-date-picker>
                            </el-form-item>
                        </el-form-item>
                        <el-form-item label="" required>
                            <el-form-item prop="deliveryTime">
                                <el-date-picker 
                                                type="date" 
                                                placeholder="" 
                                                v-model="dto.deliveryTime" 
                                                value-format="yyyy-MM-dd"
                                                style="width: 100%;">
                                </el-date-picker>
                            </el-form-item>
                        </el-form-item>
                        <el-form-item label="" prop="developer">
                            <el-input v-model="dto.developer"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="designOrgnation">
                            <el-input v-model="dto.designOrgnation"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="selfManagement">
                            <el-select v-model="dto.selfManagement" placeholder="">
                                <el-option v-for="item in options.isselfTube" :key="item.value" :label="item.label" :value="item.value"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="selfBuilt">
                            <el-select v-model="dto.selfBuilt" placeholder="">
                                <el-option v-for="item in options.isoneself" :key="item.value" :label="item.label" :value="item.value"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="decorationStandard">
                            <el-select v-model="dto.decorationStandard" placeholder="">
                                 <el-option v-for="item in options.standard" :key="item.value" :label="item.label" :value="item.value"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="remark" class="width xiangqing">
                            <el-input type="textarea" v-model="dto.remark"></el-input>
                        </el-form-item>
                        </div>
                    </el-form>


                </div>
            </div>
        </div>
    </div>
    <!--  -->
</template>
<script>
import {mapState,mapGetters,mapActions,mapMutations} from 'vuex'
import {tool} from '../../static/js/tool'
import http from '@/api/common.js'
export default {
    name:'quartsmessage',
    data() {
      return {
        classA:'active',
        classB:'moren',
        guishu:'',//
        area_prepar:'',//
        isoneself:'',//
        isselftube:'',//
        standard:'',//
        address:'',//
        districtCode:'',//
        rules: tool.rules,
        options:{
            communityOwner:[],
            area_prepar:[],
            isoneself:[],
            isselftube:[],
            standard:[],
            area_prepar:[]
        },
        dto:{
            address: "",//
            area: "",//
            belongArea: "",//
            code: '',//
            community: "1",//
            constructorProjectGuid: "string", //  ---
            decorationStandard: "",//
            deliveryTime: "",//
            designOrgnation: "",//    
            developer: "",//
            fileCode: "string",//---
            finishConstructionTime: "",//
            guid: "string", //
            houseCount: '',//
            landArea: '',//
            landNo: '',//
            name: "",//
            parentGuid: "string", //
            picCode: "string",    //
            remark: "",//
            reportGuid: "string",   //
            roadName: "",//
            selfBuilt: '',//
            selfManagement: '',//
            selfSale: true,//
            serialNo: "string",//
            startConstructionTime: "",//
            state: 0,//
            street: "",//
            surveyProjectGuid: "string",//
            totalCoveredArea: '',//
            usedCode: "string",//
            usedName: "string",//
            volumeRatio: ''//
        },
        menus:[
                {
                  menuName:'',
                  imgSrc:'',
                },
                {
                  menuName:'',
                  imgSrc:'',
                },
                {
                  menuName:'',
                  imgSrc:'',
                },
                {
                  menuName:'',
                  imgSrc:'',
                },
                {
                  menuName:'',
                  imgSrc:'',
                },
            ]
      };
    },
    watch:{
 guishu (val, oldval) {
console.log(val);
                console.log(oldval);
 }
    },
    mounted:function (){  
    },
    methods: {
         submitForm(formName) {
              let dto = this.dto;
              console.log(dto)
            this.$refs[formName].validate((valid) => {
            if (valid) {
                this.addAuarters()
            } else {
                console.log('error submit!!');
                this.open('');
                return false;
            }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        //
         open(title) {
            this.$message(title);
        },
        //
        checkOrAdd(guid){
            if(guid) {
                //
                console.log('guid',guid);
                let communityGuid = guid;
                let params = {
                    communityGuid : guid
                }
                http.checkQuery1(params).then(res => {
                    console.log(res);
                    this.$store.commit('add_quartersObj',res);
                    this.dto = res;
                    
                    if(res.selfManagement) {
                        this.dto.selfManagement = '';

                    }else{
                        this.dto.selfManagement = '';
                    }
                    if(res.selfBuilt){
                         this.dto.selfBuilt = '';
                    }else{
                        this.dto.selfBuilt = '';
                    }
                    // that.options.area_prepar = tool.changeData(res);
                });
            }else{
                //
            }
        },
       //
        getArea(){
            var that = this;
            let params = {
                area:1
            };
            http.getArea(params).then(res => {
                 that.options.area_prepar = tool.changeData(res);
            });
           
        },
       
        //-
        addAuarters(){
            var that = this;
            let dto = that.dto;
            dto.deliveryTime = dto.deliveryTime.substr(0,10);
            dto.startConstructionTime = dto.startConstructionTime.substr(0,10);
            dto.finishConstructionTime = dto.finishConstructionTime.substr(0,10);
            dto.selfBuilt == "" ? dto.selfBuilt = "true": dto.selfBuilt = "false";
            dto.selfManagement == "" ? dto.selfManagement = "true": dto.selfManagement = "false";
            http.addAuarters(dto).then(res => {
                that.open('');
                var guid = res.guid;
                this.$store.commit('add_quartersObj',res);
                //that.dto.address = res[0].address;
            });
        },
        //
        getAddress() {
            var that = this;
            let params = {};
            that.dto.address = that.dto.street + that.dto.roadName;
          
        },
     
        //-
        getDistrictCode(){
            var that = this;
            let params = {};
            http.getDistrictCode(params).then(res => {
                    console.log('',res)
                    that.dto.code = res[0];
            });
        
        },
        //
        getStandard(){
            var that = this;
            let params = {
                decorationStandard:1
            };
            http.getStandard(params).then(res => {
                    that.options.standard = tool.changeData(res);
            });
        
        },
        ////
        getIsselfTube(){
            var that = this;
            let params = {
                selfManagement:1
            };
            http.getIsselfTube(params).then(res => {
                console.log('',res);
                     that.options.isselfTube = tool.changeData(res);
                     console.log(that.options.isselftube);
            });
            
        },
        //
        getIsoneself(){
             var that = this;
            let params = {
                selfBuilt:1
            };
            http.getIsoneself(params).then(res => {
                 that.options.isoneself = tool.changeData(res);
            });
        },
        //
        getArea(){
            var that = this;
            let params = {
                area:1
            };
            http.getArea(params).then(res => {
                 that.options.area_prepar = tool.changeData(res);
            });
           
        },
        //
        getCommunityOwner(){
            var that = this;
            let params = {
                districtBelong:1
            };
            http.getCommunityOwner(params).then(res => {
                that.options.communityOwner = tool.changeData(res);
            });
           
        },
    },
    created(){
        //
        this.getArea();
        //
        this.getStandard();
        //
        this.getCommunityOwner();
        //
        this.getArea();
        //
        this.getIsoneself();
        //ss
        this.getIsselfTube();
        this.checkOrAdd(this.$router.history.current.query.guid)
         console.log(this.$router.history.current.query.guid);
        //  var params = this.$route.params;
        //  this.form.batchName = params.batchName;
        //  this.form.chooseDate = params.date1;
        //  this.form.endTime = params.date2;
        //  this.form.queueName = params.queueName;
    },
     computed:{
     ...mapState({
        quartersObj: state => state.quartersObj
     })
   },
    
  };
</script>
<style lang="scss" scoped>
#quartsMessage{
    &>h2 {
        width: 100%;
        height: 40px;
        background: #FFF;
        p {
            display: inline-block;
            float: left;
            width: auto;
            height: 40px;
            cursor: pointer;
            box-sizing: border-box;
            padding: 0 10px;
            span {
                float: left;
                display: inline-block;
                cursor: pointer;
                height: 40px;
                line-height: 40px;
                font-size: 12px;
                text-align: center;
                color: #4b7ff8;
                width: auto;
                font-weight: 400;
            }
            .right_content h2 p span:first-child {
                width: 60px;
            }
            .close_tab {
                width: 20px;
                height: 40px;
                margin-left:10px;
                background: url('../../static/images/close2.png') no-repeat center center;
            }
            .close_tab:hover{
                background: url('../../static/images/close3.png') no-repeat center center;
            }
        }
        p.active {
            background: #d3e4fa;
            border-bottom: 2px solid #0476f2;
        }
        p.moren{
                background:rgba(247, 247, 247, 1);
                span{
                    color:rgba(153, 153, 153, 1);
                    font-weight: 400;
                }
                .close_tab {
                    background: url('../../static/images/close3.png') no-repeat center center;
                }
            }
    }
    .content_d{
        width: calc(100% - 239px);
        float: left;
        margin: 20px 0px 0 15px;
        background: #fff;
        border: 1px solid #ccc;
        box-shadow: 0 0 3px #ccc;
        h2 {
            width: 100%;
            height: 35px;
            background-color: #f5f7fa;
            p {
                display: inline-block;
                width: auto;
                padding: 0;
                border-top: 2px solid #0471dd;
                background: #fff;
                height: 35px;
                .tab_name {
                    display: inline-block;
                    box-sizing: border-box;
                    float: left;
                    height: 35px;
                    line-height: 35px;
                    padding: 0 20px;
                    color: #0471dd;
                    font-weight: 400;
                    font-size: 13px;
                    width: auto;
                }
                .close_img {
                    width: 20px;
                    height: 35px;
                    line-height: 35px;
                    display: inline-block;
                    float: left;
                    box-sizing: border-box;
                    background: url('../../static/images/close3.png') no-repeat left center;
                }
            }
        }
    }
    
    
    
 .changci_content_left {
                width:100%;
                float: left;
                background: #fff;
                box-sizing: border-box;
                padding: 20px 18px;
                &>p {
                    margin-bottom: 20px;
                    button {
                     
                        color: #fff;
                        border-radius: 2px;
                        padding: 5px 15px;
                        background: #169bd5;
                    }
                    button:first-child {
                        margin-right: 20px;
                    }
                }
                .quarterMessage_box{
                    width:100%;
                    height: auto;
                    margin-top: 36px;
                    /deep/ .demo-ruleForm{
                        .el-form-item.is-required:not(.is-no-asterisk)>.el-form-item__label:after{
                            content: '*';
                            color: #f56c6c;
                            margin-left: 4px;
                        }
                        .el-form-item.is-required:not(.is-no-asterisk)>.el-form-item__label:before{
                            content: '';
                        }
                        .el-form-item{
                            width: 50%;
                            float: left;
                            margin-bottom: 0px;
                            .el-form-item__content{
                                    // width: 50% !important;
                                    line-height: 30px;
                                    text-align: center;
                                    margin-left: 50%!important;
                                .shengcheng{
                                    // width: 77%;
                                    
                                }
                                /deep/ .el-button{
                                    position: absolute;
                                    top: 5px;
                                    right: 5px;
                                }
                                .save{
                                    top: -40px;
                                 left: 0px;
                                }
                                .danwei{
                                    // width: 83%!important;
                                    
                                }
                                .ping{
                                    position: absolute;
                                    top: 5px;
                                    right: 5px;
                                }
                                .address{
                                    width:100%;
                                }
                                .el-form-item{
                                    width:100%;
                                }
                                .el-select{
                                    width: 100%;
                                }
                                .el-input__icon{
                                        line-height: 30px!important;
                                    }
                                .el-form-item__content{
                                    margin-left:0px !important;
                                    
                                    
                                }
                                /deep/ .el-input{
                                         border-right: 1px solid rgb(222, 230, 249);
                                border-bottom: 1px solid rgb(222, 230, 249);
                                    }
                                .el-input__inner{
                                    height: 38px;
                                    line-height: 38px;
                                    border:none;
                                }
                                .el-textarea{
                                     border-right: 1px solid rgb(222, 230, 249);
                                border-bottom: 1px solid rgb(222, 230, 249);
                                border-top: 1px solid rgb(222, 230, 249);
                                    textarea{
                                        border:none;
                                    }
                                    
                                }
                                .el-form-item__error{
                                    display: none;
                                }
                            }
                            .el-form-item__label{
                                width:50%!important; 
                                line-height: 38px;
                                background: rgb(240, 248, 255);
                                text-align: center;
                                border-right: 1px solid rgb(222, 230, 249);
                                border-bottom: 1px solid rgb(222, 230, 249);
                            }
                        }
                        .readyOnly{
                            .el-input__inner{
                                background: rgb(245, 247, 250);
                            }
                        }
                        .width{
                            width: 100%;
                            .el-form-item__label{
                                width: 25% !important;
                            }
                            .el-form-item__content{
                                width:75% !important;
                                margin-left: 25% !important;
                            }
                        }
                        .xiangqing{
                            .el-form-item__label{
                                line-height: 53px !important;
                            }
                        }
                        .button{
                            .el-form-item__content{
                                margin-left:0px !important;
                                float:none !important;
                            }
                            
                        }
                    }
                    .messagetable{
                        border-top:1px solid #000;
                        border-left:1px solid #000;
                        tbody tr{
                        
                            td{
                                border-right: 1px solid #000;
                                border-bottom: 1px solid #000;
                                height:30px;
                                line-height: 30px;
                                text-align: center;
                                input{
                                    border:1px solid #ccc;
                                    height: 24px;
                                }
                                i{
                                    color:red;
                                    margin-right:15px;
                                }
                                span.shengcheng{
                                    display: inline-block;
                                    width: 48px;
                                    height: 25px;
                                    text-align: center;
                                    line-height: 25px;
                                    float: right;
                                    margin-right: 10px;
                                    background: #169bd5;
                                    color:#fff;
                                    border-radius: 4px;
                                }
                                span.danwei{
                                    float: right;
                                }
                                select{
                                    width:96%;
                                    height: 26px;
                                }
                            }
                        }
                    }
                }
           
                
            }
        }
</style>



